// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// Translate
import { TranslateModule } from '@ngx-translate/core';
import { PartialsModule } from '../../partials/partials.module';
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService} from '../../../core/_base/crud';
// Shared
import { ActionNotificationComponent } from '../../partials/content/crud';
import { CodePreviewModule } from '../../partials/content/general/code-preview/code-preview.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
// Components
import { MembershipComponent } from './membership.component';
import { PackagesComponent } from './packages/packages.component';
import { ActivityComponent } from './activities/activities.component';
import { PackagesEditDialogComponent } from './packages/packages-edit/packages-edit.dialog.component';
import { ActivityEditDialogComponent } from './activities/activities-edit/activities-edit.dialog.component';
// import { FileSelectDirective } from 'ng2-file-upload';
//guard
import {PermissionGuard} from '../../../core/auth/_guards/permission.guard'
import { FlatpickrModule } from 'angularx-flatpickr';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
//import { FileUploadModule } from '../../../../../node_modules/ng2-file-upload/file-upload/file-upload.module.d';
import { SharedModule } from '../sharing.module';
// Material
import {
	MatInputModule,
	MatPaginatorModule,
	MatProgressSpinnerModule,
	MatSortModule,
	MatTableModule,
	MatSelectModule,
	MatMenuModule,
	MatProgressBarModule,
	MatButtonModule,
	MatCheckboxModule,
	MatDialogModule,
	MatTabsModule,
	MatNativeDateModule,
	MatCardModule,
	MatRadioModule,
	MatIconModule,
	MatDatepickerModule,
	MatExpansionModule,
	MatTreeModule,
	MatAutocompleteModule,
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatSnackBarModule,
	MatTooltipModule
} from '@angular/material';
import {
	usersReducer,
	UserEffects
} from '../../../core/auth';
import { FileUploadModule } from 'ng2-file-upload';

const routes: Routes = [
	{
		path: '',
		component: MembershipComponent,
		children: [
			{
				path: '',
				redirectTo: 'packages',
				pathMatch: 'full',
				data:{title:'packages'},


			},
			{
				path: 'packages',
				component: PackagesComponent,
				data:{title:'packages'},
				canActivate:[PermissionGuard]
			}
			,
			{
				path: 'package',
				component: PackagesComponent,
				data:{title:'packages'},
				canActivate:[PermissionGuard]
			},
			{
				path: 'activities',
				component: ActivityComponent,
				data:{title:'activities'},
				canActivate:[PermissionGuard]
			}
			,
			{
				path: 'activitie',
				component: ActivityComponent,
				data:{title:'activities'},
				canActivate:[PermissionGuard]
			},
		]
	}
];

@NgModule({
	imports: [
		CommonModule,
		HttpClientModule,
		SharedModule,
		PartialsModule,
		RouterModule.forChild(routes),
		StoreModule.forFeature('users', usersReducer),
    EffectsModule.forFeature([UserEffects]),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
    MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatExpansionModule,
		MatTabsModule,
		MatTooltipModule,
		MatDialogModule,
		CodePreviewModule,
		MaterialPreviewModule,
		MatTreeModule,
		NgbModalModule,
		FlatpickrModule.forRoot(),
		CalendarModule.forRoot({
			provide: DateAdapter,
			useFactory: adapterFactory
		}) , 
		FileUploadModule 
	],
	providers: [
		InterceptService,
		{
        	provide: HTTP_INTERCEPTORS,
       	 	useClass: InterceptService,
			multi: true
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: 'kt-mat-dialog-container__wrapper',
				height: 'auto',
				width: '900px'
			}
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
		PermissionGuard
	],
	entryComponents: [
		PackagesEditDialogComponent,
		ActivityEditDialogComponent
	],
	declarations: [
		MembershipComponent,
		PackagesComponent,
		PackagesEditDialogComponent,
		ActivityComponent,
		ActivityEditDialogComponent,
		// FileSelectDirective
	]
})
export class MembershipModule {}
