import { AfterViewInit, AfterViewChecked } from '@angular/core';
// Angular
import { Component ,OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay, map } from 'rxjs/operators';
import { fromEvent, merge, Observable, of, Subscription } from 'rxjs';
// LODASH
import { each, find } from 'lodash';

// NGRX
import { Store, select } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';

// Services
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../core/_base/crud';
import { MembershipService } from '../../../../core/auth/_services'
import { ActivityEditDialogComponent } from './activities-edit/activities-edit.dialog.component';

import { SubheaderService } from '../../../../core/_base/layout';


// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'kt-activities',
	templateUrl: './activities.component.html',
	styleUrls: ['../membership.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ActivityComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource;
	listData:MatTableDataSource<any>;
	selection = new SelectionModel<any>(true, []);
	displayedColumns = [ 'id', 'title' ,'discriptions' ,'type','status', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
//@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	SearchKey: string;
	selectedUser;
	hasMessages: boolean = false;
	checkdata: boolean = false;
	// Subscriptions
	private subscriptions: Subscription[] = [];

	/**
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param store: Store<AppState>
	 * @param router: Router
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param subheaderService: SubheaderService
	 */
	constructor(
		private activatedRoute: ActivatedRoute,
		private store: Store<AppState>,
		private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		public dialog: MatDialog,
		private ser: MembershipService,
	    private changeDetectorRefs: ChangeDetectorRef

	) { }
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		//this.dataSource=[];
		this.getallactivities()
	}

	ngAfterViewInit()
	{
		this.getallactivities()

	}
	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}
	addInvitations() {
		const dialogRef = this.dialog.open(ActivityEditDialogComponent, { data: {} });
	}

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasMessages = false;
	}


	getallactivities(pages = 1, prepage = 30, search = null) {

		this.ser.getallactivities(pages, prepage, search).subscribe(

			res => {
               console.log(res['result'])
				if (res['result']) {
					this.dataSource = new MatTableDataSource( res['data']);
					this.changeDetectorRefs.detectChanges();
				}
			}
		)
	}

	editactivities(id) {
		const dialogRef = this.dialog.open(ActivityEditDialogComponent, { data: {id} });
	}

	deactiveactivities(id,status){
		this.ser.updateactivities(id,{status}).subscribe(
		res=>{
			if(this.router.url == '/default/membership/activities')
				this.router.navigateByUrl("/default/membership/activitie");
			else this.router.navigateByUrl("/default/membership/activities");
		}
	 )
	}

	applyFilter() {

		//this.listData.filter=this.SearchKey.trim().toLowerCase()
		console.log(this.searchInput.nativeElement.value)
		this.getallactivities(null, null, this.searchInput.nativeElement.value)
	}
	onSearchClear() {
		this.searchInput.nativeElement.value = ''
		this.applyFilter()
	}
	nextPage(event) {

		this.getallactivities(event.pageIndex + 1, event.pageSize, null)
	}
	//checked if there is inivitations or not
	checkedinvitations() {
		if (this.dataSource) {
			if (this.dataSource.data) {
				return true
			}
			else return false
		}
		else false
	}
	checkedpermission()
	{
		if(localStorage.getItem('user'))
		{
			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge'])
			{

			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement'])
			{

			  if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement']['Invitation'])
			  return true
			}
		}
		}
		return false
	}
	fetchUsers(){

	}
}
