// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy,   ViewChild,TemplateRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// Lodash
import { Router } from '@angular/router';
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription} from 'rxjs';
// Lodash
import { each, find, some } from 'lodash';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';
import {CalendarEvent,CalendarEventAction,CalendarEventTimesChangedEvent,CalendarView} from 'angular-calendar';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { delay } from 'rxjs/operators';
import { Activity } from './activities.model';
import { MembershipService,UserService } from '../../../../../core/auth/_services';
import {  FileUploader, FileSelectDirective } from 'ng2-file-upload/ng2-file-upload';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
	selector: 'kt-activities-edit-dialog',
	templateUrl: './activities-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.Default,
})
export class ActivityEditDialogComponent implements OnInit, OnDestroy {
  @ViewChild('modalContent',) modalContent: TemplateRef<any>;
  view: CalendarView = CalendarView.Month;
  CalendarView = CalendarView;
  viewDate: Date =new Date();
  startDay: number =1;
  modalData: {
    action: string;
    event: CalendarEvent;
  };
	events: CalendarEvent[];
  activeDayIsOpen: boolean = true;
  selectedTimes : any;
  options:string='';
  start_date : Date =new Date(1559692800000);
  selectedTab: number = 0;


	// Public properties
	activity : Activity;
	activity$: Observable<Activity>;
	hasFormErrors: boolean = false;
	errormessage : string ='';
	viewLoading: boolean = false;
	loadingAfterSubmit: boolean = false;
	imageprivew : any;
	confirm : boolean = false;
	allbrancandunits : [];
	allbranch : any;
	allunits : any;
	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<ActivityEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	constructor(private modal: NgbModal,public dialogRef: MatDialogRef<ActivityEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private store: Store<AppState>,private ser :MembershipService,private userservice:UserService,private router: Router,
		private layoutUtilsService: LayoutUtilsService) { }
		public uploader: FileUploader = new FileUploader({url: this.ser.upload_file(), itemAlias: 'upload'});

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.events = [];this.allbranch=[];this.allunits=[];
		const newInviation = new Activity();
		this.activity$ = of(newInviation);
		this.activity$.subscribe(res => {
			if (!res) {
				return;
			}
			this.activity = res;
		});

		this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
		this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
			var responsePath = JSON.parse(response);
			if(responsePath.result) this.activity.image = responsePath.image;
			this.confirm = true;
		};

		this.getbranceandunits();

		if(this.data.id != undefined)
			this.getActivitiesById(this.data.id);

	}

	branchOnchange(key){
		var allunits = [];
		Object.values(this.allbrancandunits).forEach(function(units) {
			if(units['type'] == 'units' && units['branch_key'] == key) allunits.push(units);
		});
		console.table(key);console.table(allunits);
		this.allunits = allunits;
	}
	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}



	/**
	 * Save data
	 */
	onSubmit() {
		if(this.activity.title && this.activity.session_time &&  this.activity.type){
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		console.table(this.activity);
		if(this.data.id != undefined){
			this.ser.updateactivities(this.data.id,this.activity).subscribe(
 		 	res=>{
 				this.redirctFunction(res['result'],"Activity Add Successfully");
 		 	}
 		 )
		}else{
			this.ser.addactivities(this.activity).subscribe(
 		 	res=>{
 				this.redirctFunction(res['result'],'Activity Doesn\'t Added');
 		 	}
 		 )
		}
	 }else {
		 this.hasFormErrors = true;
		 this.errormessage = "Please Enter all Required Fileds"
	 }

	}


	 redirctFunction(done,msg=''){
		if(done){
			this.dialogRef.close({});
			if(this.router.url == '/default/membership/activities')
				this.router.navigateByUrl("/default/membership/activitie");
			else this.router.navigateByUrl("/default/membership/activities");
			setTimeout(()=>{
				this.layoutUtilsService.showActionNotification(msg, MessageType.Update);
			}, 1000)
		}else{
			this.loadingAfterSubmit = false;
			this.viewLoading = false;
			this.hasFormErrors = true;
			this.errormessage = msg;
		}
	}

	//get branch and units
	getbranceandunits(){
		this.userservice.getAllBrachsAndUnits(1,10000).subscribe(
			res=>{
				if(res['result'] && res['data'].length > 0){
					this.allbrancandunits = res['data'];
					var branches = [];
					Object.keys(res['data']).forEach(function(key) {
						if(res['data'][key].type=='branch') branches.push(res['data'][key]);
					});
					console.table(branches);
					this.allbranch = branches;
					//console.table(branches);
				}
			}
		)
	}


	closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

	//get activities data
	getActivitiesById(id){
		this.ser.getactivitiesById(id).subscribe(
			res=>{
				if(res['result'] && res['data'].length > 0){
					this.activity.branch_key =  res['data'][0].branch_key;
					this.activity.units_key =  res['data'][0].units_key;
					this.activity.title =  res['data'][0].title;
					this.activity.fees =  res['data'][0].fees;
					this.activity.taxs =  res['data'][0].taxs;
					this.activity.payment_startegy =  res['data'][0].payment_startegy;
					this.activity.type =  res['data'][0].type;
					this.activity.discount =  res['data'][0].discount;
					this.activity.start_discount =  res['data'][0].start_discount;
					this.activity.end_discount =  res['data'][0].end_discount;
					this.activity.image =  res['data'][0].image;
					this.activity.discriptions =  res['data'][0].discriptions;
					this.activity.session_time =  res['data'][0].session_time;
					this.activity.users_count =  res['data'][0].users_count;
					this.activity.session_count =  res['data'][0].session_count;
					this.activity.start_date =  res['data'][0].start_date;
					this.activity.end_date =  res['data'][0].end_date;
					this.activity.schedual_time =  res['data'][0].schedual_time;
					this.activity.additional_visits =  res['data'][0].additional_visits;
					this.activity.additional_service =  res['data'][0].additional_service;
					this.activity.additional_invitation =  res['data'][0].additional_invitation;
					this.imageprivew = res['data'][0].image;
					this.confirm = true;
					this.branchOnchange(this.activity.branch_key);
					this.setDataToCalender(this.activity.schedual_time);
				}
			}
		);
	}
	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}


	handleUpload(event:any) {
	this.confirm = false;
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();
    reader.onload = (event: ProgressEvent) => {
      this.imageprivew = (<FileReader>event.target).result;
    }
    reader.readAsDataURL(event.target.files[0]);
  }
}

	/** UI */
	/**
	 * Returns component title
	 */
	getTitle(): string {
		return 'New Activity';
	}

	isTitleValid(): boolean {
		return true;
	}

	setDataToCalender(data){
  //data = JSON.parse(data);
	Object.values(data).forEach(function(item){
			var start_date= new Date(item.start);
			var end_date = new Date(item.end);
			var title = this.gethoursandminutes(start_date)+" to "+this.gethoursandminutes(end_date);
			var color = colors.blue;

			console.log(start_date,end_date);
			this.events.push({start:start_date,end:end_date,title,color});
		}.bind(this));
    console.log(this.events);
	}

  dayClicked(event){
    //check if day in range
    var date = new Date(event.date);
    var start_date = new Date(this.activity.start_date).getTime();
    var end_date = new Date(this.activity.end_date).getTime();
    if(date.getTime() >= start_date && date.getTime() <= end_date){
      if(event.events.length){
		  
        var d = new Date(event.events[0].start);
        event.from_time = this.gethoursandminutes(d,false);

        var d = new Date(event.events[0].end);
        event.to_time = this.gethoursandminutes(d,false);
        
      }

      var action = date.getDate()+" - "+(date.getMonth() + 1)+" - "+date.getFullYear();
	  this.modalData = { event, action };
	  console.log(this.modalData)
      this.modal.open(this.modalContent, { size: 'lg' });
    }else alert('This Day Not In Range Of Activity');
  }

  deleteEvent(event){
    var from_time = event.date.toString().replace('00:00:00',event.from_time+':00');
    var to_time = event.date.toString().replace('00:00:00',event.to_time+':00');
    var data =this.activity.schedual_time;
    Object.keys(data).forEach(function(key){
      event.events[0].start = new Date(from_time);
      event.events[0].end = new Date(to_time);
      if(new Date(data[key].start).toString().substring(0,10) == event.events[0].start.toString().substring(0,10)){
        this.activity.schedual_time.splice(key, 1);
      }
    }.bind(this));
    event.events = [];
  }

  saveEvent(event){
    var from_time = event.date.toString().replace('00:00:00',event.from_time+':00');
    var to_time = event.date.toString().replace('00:00:00',event.to_time+':00');
	console.log(event,this.activity.schedual_time)
	if(!this.activity.schedual_time) this.activity.schedual_time=[];
    if(event.events.length){
      var data =this.activity.schedual_time;
        Object.keys(data).forEach(function(key){
			console.log('fff')
          //this.events=[];
          event.events[0].start = new Date(from_time);
          event.events[0].end = new Date(to_time);
          if(new Date(data[key].start).toString().substring(0,10) == event.events[0].start.toString().substring(0,10)){
			  console.log(data[key].start)
            this.activity.schedual_time[key].start = new Date(from_time).getTime();
            this.activity.schedual_time[key].end = new Date(to_time).getTime();
          }
          this.events.push({start:new Date(from_time),end:new Date(to_time)})
        }.bind(this));
    }
    else {
      if(this.activity.schedual_time.length < this.activity.session_count){
        this.activity.schedual_time.push({start:new Date(from_time).getTime(),end:new Date(to_time).getTime()})
        event.events.push({start:new Date(from_time).getTime(),end:new Date(to_time).getTime()})
      }else alert('you have select '+this.activity.schedual_time.length+ ' Times')
    }

  }

	gethoursandminutes(date,getampm=true){

    var hours = date.getHours();
     var minutes = date.getMinutes();
     var ampm = hours >= 12 ? 'pm' : 'am';
     hours = hours % 12;
     hours = hours ? hours : 12; // the hour '0' should be '12'
     minutes = minutes < 10 ? '0'+minutes : minutes;
     hours = hours < 10 ? '0'+hours : hours;
     if(!getampm) var strTime = hours + ':' + minutes;
     else var strTime = hours + ':' + minutes + ' ' + ampm;
     return strTime;
  }

  nexttab(index){
    this.selectedTab = index;
  }

}
