import { BaseModel } from '../../../../../core/_base/crud';

export class Packages extends BaseModel {
        club_key: string;
        branch_key: string;
        units_key: string;
        title: string;
        month_count: string;
        month_system: string;
        fees: string;
        taxs: string;
        renew_fees: string;
        renew_taxs: string;
        renew_discount: string;
        payment_startegy: string;
        type: string;
        discount: string;
        start_discount: string;
        end_discount: string;
        image: string;
        discriptions: string;
        session_time: string;
        day_per_week: string;
        additional_visits: string;
        additional_service: string;
        additional_invitation: string;
        status: string;

    clear(): void {
      this.club_key = '';
      this.branch_key = '';
      this.units_key = '';
      this.title = '';
      this.month_count = '';
      this.month_system = '';
      this.fees = '';
      this.taxs = '';
      this.payment_startegy = '';
      this.type = '';
      this.discount = '';
      this.start_discount = '';
      this.end_discount = '';
      this.image = '';
      this.discriptions = '';
      this.session_time = '';
      this.day_per_week = '';
      this.additional_visits = '';
      this.additional_service = '';
      this.additional_invitation = '';
      this.status = '';
    }
}
