import { AfterViewInit, AfterViewChecked } from '@angular/core';
// Angular
import { ViewEncapsulation, Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay, map } from 'rxjs/operators';
import { fromEvent, merge, Observable, of, Subscription } from 'rxjs';
// LODASH
import { each, find } from 'lodash';

// NGRX
import { Store, select } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';

// Services
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../core/_base/crud';
import { MembershipService } from '../../../../core/auth/_services'
import { PackagesEditDialogComponent } from './packages-edit/packages-edit.dialog.component';

import { SubheaderService } from '../../../../core/_base/layout';
import {
	MatSnackBar,
	MatSnackBarConfig,
	MatSnackBarHorizontalPosition,
	MatSnackBarVerticalPosition,
} from '@angular/material';

// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'kt-packages',
	templateUrl: './packages.component.html',
	styleUrls: ['../membership.component.css'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class PackagesComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource;
	listData: MatTableDataSource<any>;
	selection = new SelectionModel<any>(true, []);
	displayedColumns = ['id', 'title', 'discriptions', 'type', 'status', 'image', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	//@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	SearchKey: string;
	selectedUser;
	hasMessages: boolean = false;
	checkdata: boolean = false;
	// Subscriptions
	private subscriptions: Subscription[] = [];
	private Url: String;
	dataLength: any;

	/**
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param store: Store<AppState>
	 * @param router: Router
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param subheaderService: SubheaderService
	 */
	constructor(
		private activatedRoute: ActivatedRoute,
		private store: Store<AppState>,
		private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		public dialog: MatDialog,
		private ser: MembershipService,
		public snackBar: MatSnackBar,
		private changeDetectorRefs: ChangeDetectorRef

	) {
		if (localStorage.getItem('user')) {
			try {
				let user = JSON.parse(localStorage.getItem('user'))
				this.Url = (user['baseurl']).trim()
			} catch (error) {

			}
		}

	}
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		//this.dataSource=[];
		this.getallpackages()
	}

	ngAfterViewInit() {
		this.getallpackages()

	}
	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}
	addInvitations() {
		if (this.checkedpermission('addmembership')) {
			const dialogRef = this.dialog.open(PackagesEditDialogComponent, { data: {} });
		}
		else {
			this.open()
		}
	}

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasMessages = false;
	}


	getallpackages(pages = 1, prepage = 30, search = null) {
		if (this.checkedpermission('getmembership')) {
			this.ser.getallpackages(pages, prepage, search).subscribe(

				res => {
					console.log(res)
					if (res['result']) {
						this.dataSource = new MatTableDataSource(res['data']);
						this.dataLength = res['data'].length;
						// Assign the paginator *after* dataSource is set
						// this.dataSource.paginator = this.paginator;
						this.changeDetectorRefs.detectChanges();
					}
				}
			)
		}
		else {
			this.open()
		}
	}

	editpackages(id) {

		if (this.checkedpermission('editmembership')) {
			const dialogRef = this.dialog.open(PackagesEditDialogComponent, { data: { id } });
		} else {
			this.open()
		}

	}

	open() {
		let message = 'Oh Snap ! You dont have permission to get this data';
		let actionButtonLabel = '';
		let action = true;
		let setAutoHide = true;
		let autoHide = 4000;
		let horizontalPosition: MatSnackBarHorizontalPosition = 'center';
		let verticalPosition: MatSnackBarVerticalPosition = 'top';

		let addExtraClass = false;
		let config = new MatSnackBarConfig();
		config.verticalPosition = verticalPosition;
		config.horizontalPosition = horizontalPosition;
		config.duration = setAutoHide ? autoHide : 0;
		//  config['extraClasses'] = addExtraClass ? ['warn-snackbar'] : undefined;
		config.panelClass = ['warn-snackbar'];
		this.snackBar.open(message, action ? actionButtonLabel : undefined, config);
	}

	deactivepackages(id, status) {
		this.ser.updatepackages(id, { status }).subscribe(
			res => {
				if (this.router.url == '/default/membership/packages')
					this.router.navigateByUrl("/default/membership/package");
				else this.router.navigateByUrl("/default/membership/packages");
			}
		)
	}
	private refreshTable() {
		this.paginator._changePageSize(this.paginator.pageSize);
	}
	applyFilter() {
		//this.listData.filter=this.SearchKey.trim().toLowerCase()
		//console.log(this.searchInput.nativeElement.value)
		this.getallpackages(null, null, this.searchInput.nativeElement.value)
	}
	onSearchClear() {
		this.searchInput.nativeElement.value = ''
		this.applyFilter()
	}
	nextPage(event) {

		this.getallpackages(event.pageIndex + 1, event.pageSize, null)
	}
	//checked if there is inivitations or not
	checkedinvitations() {
		if (this.dataSource) {
			if (this.dataSource.data) {
				return true
			}
			else return false
		}
		else false
	}
	checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}

}
