// Angular
import { ElementRef, ViewChild, Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy, ViewEncapsulation } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// Lodash
import { Router } from '@angular/router';
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription } from 'rxjs';
// Lodash
import { each, find, some } from 'lodash';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';
import {
	MatSnackBar,
	MatSnackBarConfig,
	MatSnackBarHorizontalPosition,
	MatSnackBarVerticalPosition,
} from '@angular/material';
import { delay } from 'rxjs/operators';
import { Packages } from './packages.model';
import { PackagesComponent } from '../packages.component';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { MembershipService, UserService } from '../../../../../core/auth/_services'
import { Ng2ImgMaxService } from 'ng2-img-max';
@Component({
	selector: 'kt-packages-edit-dialog',
	templateUrl: './packages-edit.dialog.component.html',
	styleUrls: ['../../membership.component.css'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	//changeDetection: ChangeDetectionStrategy.Default,
})
export class PackagesEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	package: Packages;
	mindate=Date.now()
	package$: Observable<Packages>;
	hasFormErrors: boolean = false;
	errormessage: string = '';
	viewLoading: boolean = false;
	loadingAfterSubmit: boolean = false;
	allbrancandunits: [];
	imageprivew: any;
	allbranch: any;
	allunits: any;
	response = {};
	checkedFees = false
	// Private properties
	private componentSubscriptions: Subscription;
	private token = JSON.parse(localStorage.getItem('user'))['token'];
	public uploader: FileUploader = new FileUploader({ url: this.ser.upload_file(), authToken: this.token, itemAlias: 'upload' });
	Url: any;
	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<PackagesEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	@ViewChild('inputBox') inputBox: ElementRef;
	constructor(public dialogRef: MatDialogRef<PackagesEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		public snackBar: MatSnackBar,
		private store: Store<AppState>, private ser: MembershipService, private userservice: UserService, private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		private _ng2ImgMax: Ng2ImgMaxService
	) { }
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {

		this.allbranch = []; this.allunits = []; this.imageprivew = [];
		const newInviation = new Packages();
		this.package$ = of(newInviation);
		this.package$.subscribe(res => {
			if (!res) {
				return;
			}
			this.package = res;
		});

		this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };


		this.getbranceandunits();

		if (this.data.id != undefined)
			this.getPackagesById(this.data.id);

	}

	branchOnchange(key) {
		var allunits = [];
		Object.values(this.allbrancandunits).forEach(function (units) {
			if (units['type'] == 'units' && units['branch_key'] == key) allunits.push(units);
		});
		console.table(key); console.table(allunits);
		this.allunits = allunits;
	}
	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}



	/**
	 * Save data
	 */
	onSubmit() {
		if (this.package.title && this.package.session_time && this.package.day_per_week && this.package.month_count && this.package.type) {
			this.loadingAfterSubmit = true;
			this.viewLoading = true;
			console.table(this.package);
			if (this.data.id != undefined) {
				if(this.checkedpermission('editmembership')){
				if (this.imageprivew != '') {
					this.uploader.uploadAll();
					this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
						var responsePath = JSON.parse(response);
						if (responsePath.result) {
							this.package['image'] = responsePath.data;
							this.ser.updatepackages(this.data.id, this.package).subscribe(res => { this.redirctFunction(res['result']); });
						}
						else { this.errormessage = "Error While Upload Image"; }
					};
				}
				else {
					this.ser.updatepackages(this.data.id, this.package).subscribe(res => { this.redirctFunction(res['result']); });
				}

			}
			else{
				this.open()
			}
		
		} else {
			if(this.checkedpermission('addmembership')){
				if (this.imageprivew != '') {
					this.uploader.uploadAll();
					this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
						var responsePath = JSON.parse(response);
						if (responsePath.result) {
							this.package['image'] = responsePath.data;
							this.ser.addpackages(this.package).subscribe(res => { this.redirctFunction(res['result']); });
						}
						else { this.errormessage = "Error While Upload Image"; }
					};
				}
				else {
					this.ser.addpackages(this.package).subscribe(res => { this.redirctFunction(res['result']); });
				}
			}	}
		} else {
			this.hasFormErrors = true;
			this.errormessage = "Please Enter all Required Fileds"
		}

	}
	redirctFunction(done) {
		if (done) {
			this.dialogRef.close({});
			if (this.router.url == '/default/membership/packages')
				this.router.navigateByUrl("/default/membership/package");
			else this.router.navigateByUrl("/default/membership/packages");
			setTimeout(() => {
				this.layoutUtilsService.showActionNotification("Package Add Successfully", MessageType.Update);
			}, 1000)
		} else {
			this.loadingAfterSubmit = false;
			this.viewLoading = false;
			this.hasFormErrors = true;
			this.errormessage = 'Package Doesn\'t Added';
		}
	}

	//get branch and units
	getbranceandunits() {
		if(this.checkedpermission('getallusers')){
		this.userservice.getAllBrachsAndUnits(1, 10000).subscribe(
			res => {
				if (res['result'] && res['data'].length > 0) {
					this.allbrancandunits = res['data'];
					var branches = [];
					Object.keys(res['data']).forEach(function (key) {
						if (res['data'][key].type == 'branch') branches.push(res['data'][key]);
					});
					this.allbranch = branches;
					//console.table(branches);
				}
			}
		)
		}
	}

	//get packages data
	getPackagesById(id) {
		if(this.checkedpermission('getmembership'))
		{
		this.ser.getpackagesById(id).subscribe(
			res => {
				console.log(res);
				if (res['result'] && res['data'].length > 0) {
					//	this.Url=res['data']['Url'].trim();
					res['data'][0] = res['data'][0];
					this.package.branch_key = res['data'][0].branch_key;
					this.package.units_key = res['data'][0].units_key;
					this.package.title = res['data'][0].title;
					this.package.month_count = res['data'][0].month_count;
					this.package.month_system = res['data'][0].month_system;
					this.package.fees = res['data'][0].fees;
					this.package.taxs = res['data'][0].taxs;
					this.package.payment_startegy = res['data'][0].payment_startegy;
					this.package.type = res['data'][0].type;
					this.package.discount = res['data'][0].discount;
					this.package.start_discount = res['data'][0].start_discount;
					this.package.end_discount = res['data'][0].end_discount;
					this.package.renew_fees = res['data'][0].renew_fees;
					this.package.renew_taxs = res['data'][0].renew_taxs;
					this.package.renew_discount = res['data'][0].renew_discount;
					this.package.image = res['data'][0].image;
					this.package.discriptions = res['data'][0].discriptions;
					this.package.session_time = res['data'][0].session_time;
					this.package.day_per_week = res['data'][0].day_per_week;
					this.package.additional_visits = res['data'][0].additional_visits;
					this.package.additional_service = res['data'][0].additional_service;
					this.package.additional_invitation = res['data'][0].additional_invitation;
					this.imageprivew = this.Url + res['data'][0].image;
					this.branchOnchange(this.package.branch_key);
				}
			}
		);
		}
	}

	open() {
		let message = 'Oh Snap ! You dont have permission to get this data';
		let actionButtonLabel = '';
		let action = true;
		let setAutoHide = true;
		let autoHide = 4000;
		let horizontalPosition: MatSnackBarHorizontalPosition = 'center';
		let verticalPosition: MatSnackBarVerticalPosition = 'top';

		let addExtraClass = false;
		let config = new MatSnackBarConfig();
		config.verticalPosition = verticalPosition;
		config.horizontalPosition = horizontalPosition;
		config.duration = setAutoHide ? autoHide : 0;
		//  config['extraClasses'] = addExtraClass ? ['warn-snackbar'] : undefined;
		config.panelClass = ['warn-snackbar'];
		this.snackBar.open(message, action ? actionButtonLabel : undefined, config);
	}
	checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}



	test(event) {
		console.log(event);
	}
	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	handleUpload(event: any) {
		if (event.target.files && event.target.files[0]) {
			this.viewLoading = true;
			var reader = new FileReader();
			reader.onload = (event: ProgressEvent) => {this.imageprivew = (<FileReader>event.target).result;}
			reader.readAsDataURL(event.target.files[0]);

			this._ng2ImgMax.resizeImage(event.target.files[0], 600, 600).subscribe(
				result => {
					const newImage = new File([result], result.name);
					this.uploader.clearQueue();
					this.uploader.addToQueue([newImage]);
					//this.uploader.uploadAll();
					//console.log(result);
					reader.onload = (event: ProgressEvent) => { this.imageprivew = (<FileReader>event.target).result; }
					reader.readAsDataURL(event.target.files[0]);
					this.viewLoading = false;
				},
				error => console.log(error)
			);
		}
	}
	/** UI */
	/**
	 * Returns component title
	 */

	getTitle(): string { return (this.data.id != undefined) ? 'Edit Packages' : 'New Packages'; }

	isTitleValid(): boolean {
		return true;
	}
	validateFees(fees) {
		if (!parseFloat(fees))
			this.checkedFees = true
	}

}
